.moreView {
  margin: 16px 16px 24px;
  padding: 16px 16px 32px;
  background-color: #ebeeef;
  border-radius: 4px;
}

.heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.heading :first-child {
  font-size: 24px;
  color: #002f34;
}

.heading :last-child {
  font-size: 14px;
  color: #002f34;
}

.recommendations .cards {
  display: flex;
}

.moreView .cards {
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
}

.card {
  margin-right: 15px;
  padding: 13px;
  max-width: 280px;
  width: 280px;
  height: 270px;
  display: inline-block;
  cursor: pointer;
   border: 1px solid rgba(14,4,5,0.2);
}

.card .favorite {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.card .image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card .image img {
  height: 100px;
  max-width: 100%;
  max-height: 100%;
  min-height: 100%;
  text-align: center;
}
.card .content .rate {
  margin-top: 10px;
  font-size: larger;
  font-weight: bold;
  color: #002f34;
}
.card .content  {
  font-size: medium;
}
.card .content  {
  font-size: small;
}
.name{
  font-weight: 400;
  font-size: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 10px;
    color: rgba(0,47,52,0.64);
}
.card .date  {
  display: flex;
  justify-content: flex-end;
  font-size: small;
}
.dateText{
  text-transform: uppercase;
  color: rgba(0,47,52,0.64);
    font-size: 10px;
}
.kilometer{
  text-transform: uppercase;
  color: rgba(0,47,52,0.64);
    font-size: 11px;
    justify-content: flex-end;

}

.recommendations {
  margin: 16px 16px 24px;
}

.recommendations .heading span {
  font-size: 24px;
  color: #002f34;
}
